.app-container {
  height: 100vh;
  display: flex;
  overflow: hidden;
}

.sidebar {
  color: #fff;
  background: #1e1e1e;
  width: 250px;
  padding: 10px;
  overflow-y: auto;
}

.map-grid {
  cursor: crosshair;
  background-color: #0000;
  background-image: repeating-linear-gradient(0deg, #ccc 0 1px, #0000 1px 50px), repeating-linear-gradient(90deg, #ccc 0 1px, #0000 1px 50px);
  background-position: 0 0, 0 0;
  background-repeat: repeat, repeat;
  background-size: 50px 50px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  flex: 1;
  position: relative;
  overflow: hidden;
}

.drone {
  background: red;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: absolute;
  transform: translate(-50%, -50%);
}

.waypoint {
  background: #00f;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  position: absolute;
  transform: translate(-50%, -50%);
}

.controls {
  background: #fffc;
  border-radius: 8px;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
  position: absolute;
  bottom: 10px;
  left: 280px;
}
